@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;700&family=Space+Grotesk:wght@400;600&display=swap');

:root {
  --bg: #060d1f;
  --card-bg: rgba(255,255,255,0.03);
  --border: rgba(255,255,255,0.08);
  --text: #e8f0ff;
  --muted: #92a3c9;
  --accent1: #6e8aff;
  --accent2: #0ddac5;
  --accent3: #9b6cff;
  --radius: 18px;
  --shadow: 0 18px 48px rgba(3,14,40,0.55);
}

.projects {
  padding: 60px 20px;
  display: flex;
  justify-content: center;
  animation: fadeIn .7s ease both;
}

.projects-card {
  width: 100%;
  max-width: 1000px;
  background: var(--card-bg);
  padding: 36px;
  border-radius: var(--radius);
  border: 1px solid var(--border);
  backdrop-filter: blur(10px);
  box-shadow: var(--shadow);
  position: relative;
  overflow: hidden;
}

.projects-card::before {
  content: "";
  position: absolute;
  width: 260px;
  height: 260px;
  top: -80px;
  right: -80px;
  border-radius: 50%;
  background: var(--accent1);
  filter: blur(120px);
  opacity: 0.14;
}

.projects-title {
  font-family: 'Space Grotesk';
  font-size: 34px;
  margin: 0;
  background: linear-gradient(90deg, var(--accent1), var(--accent2));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.projects-sub {
  color: var(--muted);
  margin-top: 6px;
  font-size: 1rem;
}

.projects-list {
  margin-top: 28px;
  display: flex;
  flex-direction: column;
  gap: 22px;
}

/* ---------------- Project Item ---------------- */
.project-item {
  position: relative;
  padding: 22px;
  border-radius: var(--radius);
  background: rgba(255,255,255,0.02);
  border: 1px solid rgba(255,255,255,0.05);
  overflow: hidden;
  transition: transform .35s ease, box-shadow .35s ease, border-color .35s ease;
  box-shadow: 0 16px 40px rgba(3,14,40,0.5);
}

.project-item:hover {
  transform: translateY(-10px);
  border-color: rgba(111,140,255,0.25);
  box-shadow: 0 30px 80px rgba(3,14,40,0.7);
}

/* Spotlight Glow */
.spotlight {
  content: "";
  position: absolute;
  top: -40%;
  left: -40%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(111,140,255,0.16), transparent 60%);
  opacity: 0;
  transition: opacity .4s ease;
}

.project-item:hover .spotlight {
  opacity: 1;
}

/* Badge */
.project-badge {
  position: absolute;
  top: 14px;
  right: 14px;
  background: linear-gradient(90deg, var(--accent3), var(--accent1));
  padding: 4px 12px;
  font-size: 0.75rem;
  border-radius: 6px;
  color: #fff;
  font-weight: 600;
}

/* Content */
.project-content h3 {
  margin: 0 0 6px;
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text);
}

.project-content p {
  margin: 0;
  color: var(--muted);
  font-size: 0.95rem;
}

/* Tech Stack */
.tech-stack {
  margin-top: 10px;
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.tech-pill {
  background: rgba(255,255,255,0.04);
  padding: 6px 12px;
  border-radius: 50px;
  border: 1px solid rgba(255,255,255,0.08);
  font-size: 0.8rem;
  color: var(--accent1);
  font-weight: 600;
  transition: background .3s ease, transform .3s ease;
}

.tech-pill:hover {
  background: rgba(111,140,255,0.15);
  transform: translateY(-3px);
}

/* Link */
.project-link {
  display: inline-block;
  margin-top: 12px;
  color: var(--accent2);
  text-decoration: none;
  font-weight: 700;
  font-size: 0.95rem;
  transition: opacity .3s ease, transform .3s ease;
}

.project-link:hover {
  opacity: 0.8;
  transform: translateX(4px);
}

/* Fade In Animation */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ------------------- Responsive ------------------- */
@media (max-width: 700px) {
  .projects-card { padding: 24px; }
  .project-item { padding: 18px; }
  .projects-title { font-size: 26px; }
}
