.about {
  padding: 60px 0;
  background: linear-gradient(135deg, #eef3ff, #e7edff);
}

.card {
  max-width: 1100px;
  margin: 0 auto;
  padding: 30px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 18px;
  backdrop-filter: blur(8px);
  box-shadow: 0 20px 50px rgba(0, 40, 100, 0.12);
  animation: fadeUp 0.8s ease;
}

.title {
  color: #083b8f;
  font-size: 32px;
  margin-bottom: 10px;
  position: relative;
}

.title::after {
  content: "";
  width: 60px;
  height: 3px;
  background: #083b8f;
  position: absolute;
  bottom: -6px;
  left: 0;
  border-radius: 4px;
}

.lead {
  color: #3f4f6a;
  line-height: 1.6;
  margin-bottom: 25px;
}

.sub-title {
  margin-top: 30px;
  margin-bottom: 12px;
  font-size: 22px;
  color: #0b3f80;
}

/* ---------- TIMELINE ---------- */
.timeline {
  position: relative;
  margin: 20px 0;
  padding-left: 30px;
  border-left: 3px solid #0c3f8e;
}

.timeline-item {
  margin-bottom: 25px;
  position: relative;
}

.timeline-item .circle {
  width: 14px;
  height: 14px;
  background: #0c3f8e;
  border-radius: 50%;
  position: absolute;
  left: -40px;
  top: 2px;
  box-shadow: 0 0 10px rgba(0, 50, 150, 0.5);
}

.timeline-item .content h4 {
  color: #0c3f8e;
  font-size: 17px;
}

/* INFO TABLE */
.info-table th {
  text-align: left;
  padding: 6px 4px;
  width: 130px;
  color: #4b5f78;
}

.info-table td {
  padding: 6px 4px;
  color: #2e4358;
}

/* ---------- HIGHLIGHT CARDS ---------- */
.highlight-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 18px;
  margin-top: 15px;
}

.highlight-card {
  background: linear-gradient(135deg, #f1f5ff, #edf1ff);
  padding: 18px;
  border-radius: 14px;
  border: 1px solid #d4e2ff;
  transition: 0.3s;
  box-shadow: 0 3px 10px rgba(0, 30, 80, 0.08);
}

.highlight-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px rgba(0, 30, 80, 0.2);
}

/* ---------- STRENGTH BAR ---------- */
.strength p {
  margin: 10px 0 4px 0;
  font-weight: 600;
  color: #0a3b7c;
}

.bar {
  width: 100%;
  background: #d9e5ff;
  height: 10px;
  border-radius: 50px;
  overflow: hidden;
  margin-bottom: 10px;
}

.bar span {
  display: block;
  height: 100%;
  background: #0a3b7c;
  border-radius: 50px;
  animation: grow 1s ease forwards;
}

/* ---------- ANIMATIONS ---------- */
@keyframes fadeUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes grow {
  from { width: 0; }
  to { width: inherit; }
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .timeline {
    padding-left: 20px;
  }
}
