/* ------------------------------------------------
   PREMIUM MINIMAL PORTFOLIO HOME PAGE DESIGN
   (Soft Gradients + Smooth Animations)
-------------------------------------------------- */

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Poppins:wght@300;400;600&display=swap');

:root {
  --bg: #f5f7fb;
  --text-dark: #1a1f36;
  --text-light: #55607a;
  --primary: #4f6df0;
  --secondary: #7f56d9;
  --soft: #eef1f8;
  --radius: 18px;
  --shadow-sm: 0 4px 18px rgba(0,0,0,0.06);
  --shadow-lg: 0 25px 60px rgba(0,0,0,0.10);
}

/* ------------------------------
   BACKGROUND
------------------------------ */
.home {
  padding: 70px 20px;
  background: linear-gradient(135deg, #ffffff, #eff3ff 60%);
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.home-grid {
  max-width: 1150px;
  margin: auto;
  display: grid;
  grid-template-columns: 1.1fr 0.9fr;
  gap: 50px;
  align-items: center;
}

/* ------------------------------
    LEFT SIDE
------------------------------ */
.home-title {
  font-size: 46px;
  font-weight: 700;
  font-family: Poppins;
  color: var(--text-dark);
  margin-bottom: 12px;
}

.accent {
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.home-roles {
  font-size: 18px;
  color: var(--primary);
  margin-bottom: 14px;
  opacity: 0;
  animation: fadeSlide .8s ease forwards;
  animation-delay: .2s;
}

@keyframes fadeSlide {
  from { opacity: 0; transform: translateY(10px); }
  to   { opacity: 1; transform: translateY(0); }
}

.home-desc {
  color: var(--text-light);
  line-height: 1.6;
  margin-top: 8px;
  font-size: 16px;
}

/* ------------------------------
    ABOUT BOX
------------------------------ */
.glass-box {
  background: rgba(255,255,255,0.6);
  border-radius: var(--radius);
  padding: 20px;
  margin-top: 25px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(0,0,0,0.06);
  box-shadow: var(--shadow-sm);
  transition: .3s ease;
}

.glass-box:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
}

/* ------------------------------
    TECH BADGES
------------------------------ */
.tech-box {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 22px;
}

.tech-item {
  padding: 8px 14px;
  border-radius: 50px;
  background: var(--soft);
  border: 1px solid rgba(0,0,0,0.06);
  font-weight: 600;
  font-size: 14px;
  color: var(--primary);
  display: flex;
  align-items: center;
  gap: 6px;
  transition: .3s;
}

.tech-item:hover {
  background: #e5e9ff;
  transform: translateY(-4px);
}

/* ------------------------------
    BUTTONS
------------------------------ */
.home-actions {
  margin-top: 26px;
  display: flex;
  gap: 14px;
}

.btn {
  padding: 12px 26px;
  border-radius: 50px;
  text-decoration: none;
  font-size: 15px;
  font-weight: 600;
  transition: .3s ease;
}

.primary {
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  color: white;
  box-shadow: 0 4px 14px rgba(79,109,240,0.3);
}

.primary:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 25px rgba(79,109,240,0.4);
}

.ghost {
  border: 2px solid var(--primary);
  color: var(--primary);
}

.ghost:hover {
  background: #e8ecff;
  transform: translateY(-4px);
}

/* ------------------------------
    RIGHT PROFILE CARD
------------------------------ */
.profile-card {
  background: #ffffff;
  padding: 26px;
  border-radius: var(--radius);
  border: 1px solid rgba(0,0,0,0.05);
  box-shadow: var(--shadow-lg);
  text-align: center;
  animation: fadeUpCard .9s ease;
}

@keyframes fadeUpCard {
  from { opacity:0; transform: translateY(20px); }
  to   { opacity:1; transform: translateY(0); }
}

.profile-img {
  width: 165px;
  height: 165px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid #f1f3ff;
  margin-bottom: 16px;
  animation: softFloat 4s ease-in-out infinite;
}

@keyframes softFloat {
  0%,100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

.name {
  font-size: 22px;
  font-weight: 600;
  color: var(--text-dark);
}

.location {
  color: var(--text-light);
  margin-bottom: 10px;
}

/* small text */
.small-info p {
  margin: 4px 0;
  color: var(--text-light);
  font-size: 15px;
}

/* ------------------------------
    SOCIAL ICONS
------------------------------ */
.social-links {
  margin-top: 18px;
  display: flex;
  justify-content: center;
  gap: 18px;
}

.icon {
  padding: 10px;
  background: var(--soft);
  border-radius: 50%;
  color: var(--text-dark);
  transition: .3s;
}

.icon:hover {
  background: #e2e6ff;
  color: var(--primary);
  transform: translateY(-5px);
}

/* ------------------------------
    RESPONSIVE
------------------------------ */
@media (max-width: 900px) {
  .home-grid {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .home-actions {
    justify-content: center;
  }
  .tech-box {
    justify-content: center;
  }
}
